.hide {
    display: none;
}

.SelectLeaderContainer {
    height: calc(100% - 115px);
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-align: center;
        align-items: center;
}

.SelectLeaderContainer__go {
    font-size: 100px;
    background-color: #d9d9d9;
    border-radius: 50%;
    padding: 50px;
    color: #2b2b2b;
    text-align: center;
}

.SelectLeaderContainer__resultText {
    font-size: 50px;
}

.SelectLeaderContainer__resultNum {
    color: #eb6600;
    font-size: 200px;
}

.SelectLeaderContainer__positionOne {
    border: 2px solid #eb6600;
    font-size: 50px;
    position: absolute;
    bottom: 40px;
    padding: 10px 20px;
}

.SelectLeaderContainer__triangle {
    width: 0;
    height: 0;
    border-left: 30px solid transparent;
    border-right: 30px solid transparent;
    border-top: 30px solid #eb6600;
    position: absolute;
    bottom: 12px;
}

.SelectLeaderContainer__spyPhase {
    background-color: #d9d9d9;
    border-radius: 35px;
    color: #2b2b2b;
    font-size: 50px;
    height: 75px;
    margin-top: 50px;
    padding: 0 30px;
}



