.Mission {
    height: 120px;
    width: 120px;
    border: 3px solid #d9d9d9;
    border-radius: 50%;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex-pack: center;
        justify-content: center;
    font-size: 90px;
    margin-left: 25px;
}

@-webkit-keyframes alternateColors {
  from {border: 4px solid #850000;}
  to {border: 4px solid #001885;}
}

@keyframes alternateColors {
  from {border: 4px solid #850000;}
  to {border: 4px solid #001885;}
}

.current {
    -webkit-animation-name: alternateColors;
            animation-name: alternateColors;
    -webkit-animation-direction: alternate;
            animation-direction: alternate;
    -webkit-animation-duration: 1s;
            animation-duration: 1s;
    -webkit-animation-iteration-count: infinite;
            animation-iteration-count: infinite;
    -webkit-animation-timing-function: linear;
            animation-timing-function: linear;
}

.failed {
    background-color: #850000;
}

.successful {
    background-color: #001885;
}



